* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* font-family: "Poppins", sans-serif; */
}
:root {
  --bgcolor: #f1f5fd;
  --primary-color: #6f5de7;
  --text-color: #828286;
  --main-text-color: #333333;
}
html {
  font-size: 62.5%;
}

a {
  text-decoration: none;
  color:black;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #333333;
}


/* --------------------------------- navbar css style starts 
                        -------------------------------------------------------*/

                        .navbar-bg {
                          background-color: var(--bgcolor);
                          padding: 2rem 0;
                          z-index: 99;
                          position: relative;
                        }
                        
                        .navbar-brand {
                          font-size: 2.5rem;
                        }
                        
                        .navbar li {
                          padding: 0 2rem;
                        }
                        
                        .navbar li a {
                          font-size: 1.7rem;
                          font-weight: lighter;
                          position: relative;
                        }
                        
                        .navbar li a:hover {
                          color: var(--primary-color) !important;
                          text-shadow: 0 0.2rem 0.3rem rgba(183, 171, 255, 0.5);
                        }
                        
                        .btn-style {
                          width: 12rem;
                          height: 4rem;
                          border-radius: 2rem;
                          color: #fff;
                          background-color: var(--primary-color);
                          text-transform: capitalize;
                          font-size: 1.6rem;
                        }
                        
                        .btn-style:first-child {
                          margin-right: 2rem;
                        }
                        
                        .btn-style:hover {
                          border: 0.1rem solid var(--primary-color);
                          background-color: transparent;
                          color: var(--primary-color);
                          box-shadow: 0 0.2rem 0.5rem rgba(183, 171, 255, 0.5);
                        }
                        
                        .btn-style-border {
                          border: 0.1rem solid var(--primary-color);
                          background-color: transparent;
                          color: var(--primary-color);
                          box-shadow: 0 0.2rem 0.5rem rgba(183, 171, 255, 0.5);
                        }
                        
                        .btn-style-border:hover {
                          color: #fff;
                          background-color: var(--primary-color);
                          text-transform: capitalize;
                        }
                        
                        /* --------------------------------- Navbar ends  
                                                -------------------------------------------------------*/
               
                                                
                                                /* --------------------------------- Footer  Section starts  
                        -------------------------------------------------------*/

footer {
  padding: 5rem 0;
  background-color: #182334;
  color: #fff;
}

footer h2 {
  color: #fff;
}

footer li {
  list-style: none;
  font-size: 1.5rem;
  font-weight: lighter;
  color: #fff;
  cursor: pointer;
  margin: 1rem 0;
}

footer li:hover {
  color: var(--primary-color);
}

footer .fontawesome-style {
  font-size: 2.3rem;
  color: var(--primary-color);
  background-color: transparent;
  width: 0rem;
  height: 0rem;
  margin: 2rem 0;
  cursor: pointer;
}

footer .fontawesome-style:hover {
  transition: all 1s;
  animation: rotatefont 1s linear infinite alternate-reverse;
  color: #fff;
}

@keyframes rotatefont {
  0% {
    transform: scale(0.6);
  }
  100% {
    transform: scale(1.2);
  }
}

footer .main-hero-para {
  margin: 0;
}

/* --------------------------------- Footer  Section ends  
                        -------------------------------------------------------*/

                        /* --------------------------------- Contact US Section starts  
                        -------------------------------------------------------*/
.contactus-section {
  margin: 15rem 0;
}

.contact-leftside img {
  min-width: 80%;
  max-height: 30rem;
  height: auto;
}

.contact-input-feild {
  margin-bottom: 4rem;
}

.contact-rightside form .form-control {
  border: 1px solid #ced4da;
  -webkit-appearance: none;
  height: 5rem;
  border-radius: 0.5rem;
  font-size: 1.6rem;
  padding-left: 1rem;
  font-weight: lighter;
}

.contact-rightside form .form-check-input {
  width: 2rem;
  height: 2rem;
}

.form-checkbox-style {
  margin: 4rem 0;
}

.contact-rightside form .main-hero-para {
  font-size: 1.5rem;
  color: var(--text-color);
  margin: 0;
  font-weight: lighter;
  width: 75%;
  padding: 0 2rem;
}
/* --------------------------------- Contact US Section ends  
                        -------------------------------------------------------*/


/* --------------------------------- 404 Error Page Section 
                        -------------------------------------------------------*/

                        #notfound {
                          position: relative;
                          height: 100vh;
                        }
                        
                        #notfound .notfound {
                          position: absolute;
                          left: 50%;
                          top: 50%;
                          -webkit-transform: translate(-50%, -50%);
                          -ms-transform: translate(-50%, -50%);
                          transform: translate(-50%, -50%);
                        }
                        
                        .notfound {
                          max-width: 920px;
                          width: 100%;
                          line-height: 1.4;
                          text-align: center;
                          padding-left: 15px;
                          padding-right: 15px;
                        }
                        
                        .notfound .notfound-404 {
                          position: absolute;
                          height: 100px;
                          top: 0;
                          left: 50%;
                          -webkit-transform: translateX(-50%);
                          -ms-transform: translateX(-50%);
                          transform: translateX(-50%);
                          z-index: -1;
                        }
                        
                        .notfound .notfound-404 h1 {
                          font-family: "Maven Pro", sans-serif;
                          color: #ececec;
                          font-weight: 900;
                          font-size: 276px;
                          margin: 0px;
                          position: absolute;
                          left: 50%;
                          top: 50%;
                          -webkit-transform: translate(-50%, -50%);
                          -ms-transform: translate(-50%, -50%);
                          transform: translate(-50%, -50%);
                        }
                        
                        .notfound h2 {
                          font-family: "Maven Pro", sans-serif;
                          font-size: 46px;
                          color: #000;
                          font-weight: 900;
                          text-transform: uppercase;
                          margin: 0px;
                        }
                        
                        .notfound p {
                          font-size: 16px;
                          color: #000;
                          font-weight: 400;
                          text-transform: uppercase;
                          margin-top: 15px;
                        }
                        
                        .notfound a {
                          font-size: 14px;
                          text-decoration: none;
                          text-transform: uppercase;
                          background: #189cf0;
                          display: inline-block;
                          padding: 16px 38px;
                          border: 2px solid transparent;
                          border-radius: 40px;
                          color: #fff;
                          font-weight: 400;
                          -webkit-transition: 0.2s all;
                          transition: 0.2s all;
                        }
                        
                        .notfound a:hover {
                          background-color: #fff;
                          border-color: #189cf0;
                          color: #189cf0;
                        }
                        
                        @media only screen and (max-width: 480px) {
                          .notfound .notfound-404 h1 {
                            font-size: 162px;
                          }
                          .notfound h2 {
                            font-size: 26px;
                          }
                        }
                        
                        /* --------------------------------- 404 Error Page Section  Ends
                                                -------------------------------------------------------*/
                        
                        /* --------------------------------- responsive media queries  
                                                -------------------------------------------------------*/
                        @media (min-width: 1500px) {
                          .container,
                          .container-lg,
                          .container-md,
                          .container-sm,
                          .container-xl,
                          .container-xxl {
                            max-width: 1500px !important;
                          }
                        }
                        
                        @media (min-width: 1400px) {
                          .container,
                          .container-lg,
                          .container-md,
                          .container-sm,
                          .container-xl,
                          .container-xxl {
                            max-width: 1400px;
                          }
                        }                        

